.all_cont{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.all_cont a{
    text-decoration: none;
    color: black;
}
.crypto_inf_cont{
    margin-top: 10px;
    background-color:rgb(0, 0, 0, 0.4);
    border:rgb(0, 0, 0) solid 1px;
    width: 50%;
}
.main_inf{
    display: flex;
    justify-content: space-around;
    height: 60px;
}
.symbol_cont{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.symbol_cont p{
    margin: 0;
}
.price{
    display: flex;
    justify-content: space-around;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
}
p{
    margin: 0;
}