header {
    background-color: #a4a4a4;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 6%;

}
header a {
    text-decoration: none;
    color: black;
    /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
}
.Hcont {
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    width: 80%;
    /* font-family: 'Garamond'; */

}

@media screen and (max-width: 600px){
    /* body{
        overflow-y: hidden;
        overflow-x: hidden;
    } */
    .Hcont{
        display: none;

    }
    .active{
        /* height: 100vh; */
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        /* position: absolute; */
        flex-direction: column;
        left: 0;
        z-index: 500;
        top: 0;
    }
    .active_head{
        z-index: 250;
        position: fixed;
        left: 0;
        top: 0;
        width: 50%;
        height: 100%;
        background-color: white;
        border-right: 1px solid black;
    }
    .active a{
        padding: 10px;
    }
    header{
        position: fixed;
        background-color: transparent;
        top: 0;
        left: 0;
        z-index: 100;
    }
    /*   */
}