.settings{
   display: none;
}
.span_opt{
    
    height: 40px;
    width: 40px;
    position: absolute;
    left: 3px;
    background-color: transparent;
    display: flex;
    border: none;
    z-index: 600;
    cursor: pointer;
    /* top: 40px; */
    top: 2px;

}
.span_opt span{
    
    background-color: black;
    height: 4px;
    width: 100%;
    position: absolute;
    left: 0;
    transition: all .6s;
}
.span_opt span:nth-child(1){
    top: 0;
}
.span_opt span:nth-child(2){
    top: 15px;
}
.span_opt span:nth-child(3){
    top: 15px;
}
.span_opt span:nth-child(4){
    top: 30px;
} 
.span_opt.active span:nth-child(1){
    left: 100px;
}
.span_opt.active span:nth-child(2){
    transform: rotate(45deg);
}
.span_opt.active span:nth-child(3){
    transform: rotate(315deg);
}
.span_opt.active span:nth-child(4){
    left: 100px;
}

@media screen and (max-width: 600px){
    
    .settings{
        display: block;
    }
    .person{
        /* background-color: aqua; */
        position: absolute;
        top: 2px;
        right:2px;
    }
}