body{
    overflow: hidden;
}
.mesager_container{
    width: 100%;
    height: 94%;
    display: flex;
}
.left_panel{
    height: 94vh;
    width: 30%;
    background-color: #dfdce8;
    border-right: 1px solid black;
}
.chat_container{
    height: 100%;
    width: 70%;
}
.chat{
    background-color: white;
    width: 100%;
    height: 90%;
}
.bottom_menu{
    left: 35%;
    background-color: white;
    width: 60%;
    height: 10%;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 10px;
    border-radius: 15%;
}
.top_menu{
    height: 10%;
    width: 100%;
    border-bottom: 1px solid black;
}
/* .strted_chats{
    height: 100%;
    width: 70%;
} */
.head{
    border-bottom: 1px solid black;
    width: 100%;
    height: 6%;
}
.friend_cont{
    background-color: rgba(0,0,0,0.4);
    margin: 10px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 3px;
}
.my_mesages{
    height: 30px;
    width: 30%;
    background-color: rgba(0,0,0,0.4);
    border: 1px solid black;
    margin: 5px;
    margin-left: 69%;
}
.oponenet_mesages{
    height: 30px;
    width: 30%;
    /* background-color: rgba(0,0,0,0.4); */
    border: 1px solid black;
    margin: 5px;
    margin-right: 69%;
}


.friends{
    height: 91%;
    overflow-y: scroll;
}
 @media screen and (max-width: 600px) {
   
    .left_panel{
        position: absolute;
        transform: translate(-31vw,0);
        z-index: 10;
        transition: 0.4s;
    }
    .chat_container{
        width: 100%;
        z-index: 4;
    }
    .bottom_menu{
        left: 20%;
    }
    .close_swipe{
        width: 100vw;
        height: 100vh;
      display: none;
        background-color: rgba(0,0,0,0.3);
        position: fixed;

    }
    .active{
      display: block;
      z-index: 5;
}
 }