input{
    border: none;
    border-bottom: 1px solid black;
    margin-right: 5px;
    transition: 0.5s;
    background-color: transparent;
}
input:focus{
    /* border: none; */
    outline: none;
    border-bottom: 2px solid black ;
}
button{
    background-color: transparent;
    margin: 5px;
    border: 1px solid black;
}

select{
    border: none;
    background-color: transparent;
    border-bottom: 1px solid black;
    margin: 5px;
}
select:focus{
    outline: none;
    border-bottom: 2px solid black;
}